            />
        <path class=" delay200"
           d="M 129.97065,9.0411991 V 5.7132368 h 12.00961 v 3.3279623 h -5.78776 q -0.1447,1.8810219 -0.43409,3.6173509 h 5.64307 v 3.327962 q -0.28939,14.614095 -11.86491,21.848796 V 33.92857 q 3.76205,-4.051433 5.64307,-8.536947 -1.88102,-1.157552 -3.90674,-1.591634 V 21.34019 q -1.01286,0.868165 -2.3151,1.881023 V 19.16978 q 2.60449,-3.762044 2.89388,-10.1285809 z M 123.02534,37.54592 V 17.578146 q -1.30225,0.868164 -2.89388,1.44694 v -3.327962 q 3.76204,-3.183268 4.7749,-11.2861333 h 4.77491 q -0.72347,4.6302084 -2.02572,7.6687823 V 37.54592 Z m 21.12533,-0.578776 v -3.327962 h 2.60449 q 1.73633,0 1.73633,-1.736328 V 4.2662966 H 152.977 V 32.48163 q 0.1447,4.630208 -4.63021,4.485514 z m -1.30225,-7.234701 V 7.883647 h 3.90674 v 21.848796 z m -5.64307,-13.745931 h -2.45979 q -1.15756,2.749186 -2.6045,4.196126 2.31511,0.434082 4.19613,1.012858 0.43408,-2.17041 0.86816,-5.208984 z"
