    </div>
		<h2 style="display: none">项目案例</h2>
		<p class="subTitle" :class="{fadeIn: xmalFadeIn}">{{xmalMsg}}</p>
		<ul :class="{fadeIn: xmalFadeIn}">
			<li v-for="xmalItem in xmalItems">
				<div class="proPic"><span></span><router-link to=""><img :src="xmalItem.imgSrc"></router-link></div>
				<router-link to=""><h3>{{xmalItem.h3Msg}}</h3></router-link>
				<router-link to=""><p>{{xmalItem.pMsg}}</p></router-link>
			</li>
		</ul>
		<div class="button1 intro_more_bg intro_more_over" :class="{fadeIn: xmalFadeIn}"><router-link class="intro_more_text" to="/programe">查看全部</router-link></div>

	</div>
	
	</section>
</template>

<script>
// import layoutHeader from '@/components/layoutHeader'
// import layoutFooter from '@/components/layoutFooter'

export default {
	name: 'xmal',
	components: {  },
	data () {
		return {
			xmalMsg: 'INNOCUBER经过多年积淀在复合型新消费场景与互联网产品领域拥有大量案例，基本全部落地并且获得良好市场反响。',
			xmalItems: [
				{ imgSrc: require('../../assets/HproPic1.png'),
				  h3Msg: '匠舍.菁木山舍',
				  pMsg: '匠舍打造的集休闲、养生、文化为一体复合型消费空间品牌民宿。'
				},
				{ imgSrc: require('../../assets/HproPic2.png'),
				  h3Msg: 'JUST',
				  pMsg: '提倡多元化的独立乐活方式品牌，为用户解决消费、生活、精神层面的需求。'
				},
				{ imgSrc: require('../../assets/HproPic5.png'),
				  h3Msg: '研选',
				  pMsg: '欧洲品质生活电商平台，经营符合欧盟认证、欧标值检的优质国货。'
				},
				{ imgSrc: require('../../assets/HproPic4.png'),
				  h3Msg: '小象直播',
				  pMsg: '最懂华人的泛娱乐直播平台，一款在东南亚地区冲击排行榜的产品。'
				}
			]
		}
	},
	props: [ 'xmalAnimation', 'xmalFadeIn' ]
}
</script>

<style scoped>
/*projects*/
.projects{padding:80px 0 140px; background: #f2f2f2}
.projects .container{ width: 1400px; max-width: 1120px}
.projects .svg04Box{ width: 160px; height: 90px; margin:0 auto;}
.projects h2{text-align: center; font-family: zhengcu; font-size:40px; color:#080808; 
	line-height: 40px; margin-bottom: 60px; opacity: 0}
.projects .subTitle{font-size: 16px; font-family: pingfangL;
 width:640px; margin:0 auto; text-align: center; animation-delay: 1200ms;
  margin-bottom: 50px; line-height: 32px; opacity: 0}
.projects ul{display:flex;   justify-content: space-between; align-items: center;
	margin-bottom: 30px; opacity: 0; animation-delay: 1200ms}
.projects ul li{width:270px; height:368px; background: #fff; transition: all .3s }
.projects ul li:hover{ opacity: .75; cursor: pointer; }
.projects ul li .proPic{width:270px; height:195px;
 overflow:hidden; position: relative;}
 .projects ul li .proPic img{height:100%; width:auto; position: absolute;
  top:0; left:0; right:0; margin:auto auto; display: block}
.projects ul li .proPic span{display:block; background: #000; opacity: .2;
height:100%; width:100%; position: absolute; top:0; left:0; z-index: 10}
.projects ul li h3{font-size: 18px; font-family: pingfangB; 
	padding:35px 0 20px 16px; color:#080808;}
.projects ul li p{padding:0 16px 0; font-size: 14px; font-family: pingfangL;
 color:#888888;}
.projects .button1{height:69px; width:299px; border: 1px solid #080808; background: #080808; 
 margin:0 auto; position: relative; opacity: 0;  animation-delay: 1200ms}
.projects .button1 a{display:block; width:100%; height:100%;
 text-align: center; line-height: 69px; font-size: 16px; position: relative;
  z-index: 999; font-family: pingfangB; color:#fff;}

.projects .button1::before,
.projects .button1::after{ 
    top: 0;
    width: 50%;
    height: 100%;
    background-color: #000;
    position: absolute;
    z-index: -1;
    display: block;
    content: '';
    box-sizing: border-box;
    -webkit-transition: all linear .2s;
    transition: all linear .2s;
}
.projects .button1::before{ left: 0; }
.projects .button1::after{ right: 0; }
.projects .button1:hover::before,
.projects .button1:hover::after{
	z-index: 99;/*  background: #fff; */ width: 0;
}
.projects .button1:hover{ background: #fff; color: #333 }
.projects .button1:hover a{ color: #333 }
</style>


