<template>
<div class="index_content">

	<!-- Banner组件 -->
	<banner-section />

	<!-- 我们优势组件 -->
	<wmys-section :isAnimation="isAnimation" :isFadeIn="isFadeIn" />

	<!-- 我们动态组件 -->
	<wmdt-section :wmdtAnimation="wmdtAnimation" :wmdtLeftmore="wmdtLeftmore" :wmdtFadeIn="wmdtFadeIn" />

	<!-- 项目案例组件 -->
	<xmal-section :xmalAnimation="xmalAnimation" :xmalFadeIn="xmalFadeIn" />

	<!-- 服务内容组件 -->
	<fwnr-section :fwnrAnimation="fwnrAnimation" :fwnrFadeIn="fwnrFadeIn" />
	
	<!-- 联系我们组件 -->
	<lxwm-section :lxwmAnimation="lxwmAnimation" :lxwmFadeIn="lxwmFadeIn" />
	
	<!-- index.js配置路由 -->
	<router-view />   
	
	<!-- <transition name="fade" mode="out-in"> -->
	<div id="top" class="animated" @click="click" :class="[{fadeIn:isShow},{fadeOut:!isShow}]"><i class="iconfont icon-xiangshangjiantou"></i></div>
	<!-- </transition> -->
</div>
</template>

<script>
import fwnrSection from '@/pages/index/fwnrSection'   //服务内容
import lxwmSection from '@/pages/index/lxwmSection'   //联系我们
import xmalSection from '@/pages/index/xmalSection'   //项目案例
import wmdtSection from '@/pages/index/wmdtSection'   //我们动态
import wmysSection from '@/pages/index/wmysSection'   //我们优势
import bannerSection from '@/pages/index/bannerSection'   //首页banner

export default {
	name: 'index_content',
	components: { fwnrSection, lxwmSection, xmalSection, wmdtSection, wmysSection, bannerSection },
	data () {
		return {
			isShow: false,

			//我们优势传参
			isAnimation: true,
			isFadeIn: false,

			// 我们动态传参
			wmdtAnimation: false,
			wmdtLeftmore: false,
			wmdtFadeIn: false,

			//项目案例传参
			xmalAnimation: false,
			xmalFadeIn: false,

			//服务内容传参
			fwnrAnimation: false,
			fwnrFadeIn: false,

			// 联系我们传参
			lxwmAnimation: false,
			lxwmFadeIn: false
		}
	},
	methods: {
		click() {
			$("html,body").animate({scrollTop:0},300)
		},
		_scrollTop() {
			
			// const定义的变量不可修改，const一般在require一个模块的时候用或者定义一些全局常量
			// let声明的变量作用域是在块级域中，函数内部使用let定义后，对函数外部无影响，可以在声明变量时为变量赋值，默认值为undefined,也可以稍后在脚本中给变量赋值，在生命前无法使用。
			// var 声明的变量作用域是全局的或者是函数级的
			// 
			// 
			// 把data里的变量继承过来重新封装一下，用_this（其他命名也行）来替代this
			var _this=this
		    _this.isAnimation = true
		    _this.isFadeIn = true
			// console.log(_this.isAnimation)
			$(document).scroll(function(){
				var top = $(document).scrollTop()
				// console.log("Stop="+top)
				// var docHeight = $(document).height();
			 //    var windowHeight = $(window).height();
			 //    var scrollPercent = (top / (docHeight-windowHeight)) * 100;
			    // console.log("scrollPercent="+scrollPercent)
			    
			    // 返回顶部判断条件
			      if(top>600){
			      	_this.isShow = true
			      } else {
			      	_this.isShow = false
			      }

			      // 我们动态
			      if(top<230){
			      	_this.wmdtAnimation = false
			      	_this.wmdtLeftmore = false
			      	_this.wmdtFadeIn = false
			      }
			      if(top>=500){
			      	_this.wmdtAnimation = true,
			      	_this.wmdtLeftmore = true
			      }
			      if(top>600){
			      	_this.wmdtFadeIn = true
			      }

			      //项目案例
			      if(top<1000){
			      	_this.xmalAnimation = false
			      	_this.xmalFadeIn = false
			      }
			      if(top>1300){
			      	_this.xmalAnimation = true
			      	_this.xmalFadeIn = true
			      }

			      // 服务内容
			      if(top<1955){
			      	_this.fwnrAnimation = false
			      	_this.fwnrFadeIn = false
			      }
			      if(top>2100){
			      	_this.fwnrAnimation = true
			      	_this.fwnrFadeIn = true
			      }

			      // 联系我们
			      if(top<2400){
			      	_this.lxwmAnimation = false
			      	_this.lxwmFadeIn = false
			      }
			      if(top>2600){
			      	_this.lxwmAnimation = true
			      	_this.lxwmFadeIn = true
			      }
			})
		}
	},
	mounted() {
		this._scrollTop()
	}
}
</script>



<style>


.container {
    margin: 0 auto;
    position: relative;
    max-width: 1180px;
}


#top{ width: 50px; height: 50px; border: 1px solid #333; box-shadow: 2px 2px 3px rgba(0, 0, 0, .3); position: fixed; bottom: 20px; right: 20px; opacity: 1; background: #fff; text-align: center; line-height: 50px; cursor: pointer; /*transition: all .3s*/}
.icon-xiangshangjiantou{ font-size: 34px; color: #333; }

.fade-enter-active,
.fade-leave-active{ transition: all .5s }

.fade-enter,
.fade-leave-active{ opacity: 0 }


 .leftmove {
    animation: leftmove 0.5s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 normal both;
    -webkit-animation: leftmove 0.5s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 normal both;
    opacity: 0;
}
.delay1 {
    -webkit-animation-delay: .3s;
    animation-delay: .3s;
}
.delay2 {
    -webkit-animation-delay: .5s;
    animation-delay: .5s;
}
.delay3 {
    -webkit-animation-delay: .8s;
    animation-delay: .8s;
}
@-webkit-keyframes leftmove{
	0%{ opacity: 0; transform: translate(-40%,0) }
	100%{ opacity: 1; transform: translate(0,0) }
}

@-webkit-keyframes showAnimation{
	0%{ opacity: 0 }
	100%{ opacity: 1 }
}







.delay100{ -webkit-animation-delay: 100ms; }
.delay300{ -webkit-animation-delay: 300ms; }
.delay500{ -webkit-animation-delay: 500ms; }
.delay700{ -webkit-animation-delay: 700ms; }
.delay900{ -webkit-animation-delay: 900ms; }
.delay1100{ -webkit-animation-delay: 1100ms; }
.delay1300{ -webkit-animation-delay: 1300ms; }
.delay1500{ -webkit-animation-delay: 1500ms; }
.delay1700{ -webkit-animation-delay: 1700ms; }



 .bdR, .bdL {
    animation: intro_more_btn_h 0.3s ease-in 2.8s 1 normal both;
    -webkit-animation: intro_more_btn_h 0.3s ease-in 2.8s 1 normal both;
}
.bdT, .bdB {
    animation: intro_more_btn_w 0.3s ease-in 2.8s 1 normal both;
    -webkit-animation: intro_more_btn_w 0.3s ease-in 2.8s 1 normal both;
}
@-webkit-keyframes intro_more_btn_h{
	0%{ height: 0 }
	100%{ height: 100% }
}
@-webkit-keyframes intro_more_btn_w{
	0%{ width: 0 }
	100%{ width: 100% }
}
</style>


