<style scoped>
/*我们动态*/
.news{height:728px; 
 background: url('~@/assets/HNewsPic.png') no-repeat left top; margin-bottom: 72px}
.svg05Box{ width: 370px; margin: 0 auto; text-align: center; }
#svg05{ width: 190px; height: 80px; }
.newsmin{width:370px; float:right; padding-top: 155px}
.news h2{text-align: center; font-family: zhengcu; font-size:40px; color:#080808; 
	line-height: 40px; margin-bottom: 70px; opacity: 0; animation-delay: 900ms}
.news ul{text-align: center; opacity: 0; animation-delay:800ms;}
.news ul .newsMark{font-size: 16px; color:#888888; font-family: pingfangB;
 margin-bottom: 12px}
.news ul .newsTxt{font-size: 14px; color:#080808; font-family: pingfangL;
margin-bottom: 50px}
.news .button1{height:69px; width:299px; border: 1px solid #080808; background: #080808; 
 margin:0 auto; opacity: 0; animation-delay:800ms; position: relative}
.news .button1 a{display:block; width:100%; height:100%;
 text-align: center; line-height: 69px; font-size: 16px;
  position: relative; z-index: 999;  font-family: pingfangB; color:#fff;}
.news .button1::before,
.news .button1::after{ 
    top: 0;
    width: 50%;
    height: 100%;
    background-color: #000;
    position: absolute;
    z-index: -1;
    display: block;
    content: '';
    box-sizing: border-box;
    -webkit-transition: all linear .2s;
    transition: all linear .2s;
}
.news .button1::before{ left: 0; }
.news .button1::after{ right: 0; }
.news .button1:hover::before,
.news .button1:hover::after{
	z-index: 99;/*  background: #fff; */ width: 0;
}
.news .button1:hover{ background: #fff; color: #333 }
.news .button1:hover a{ color: #333 }
</style>


