<style scoped>
/*contact*/
.maincontact{height:750px; padding-top: 150px;
 background: url('~@/assets/HSubBanner.png') no-repeat center top; 
 background-size:1920px auto; opacity: 0}
.maincontact .content{background:#fff; margin:0 auto;
 width:620px; height:600px; text-align: center; opacity: 0; animation-delay:.3s;}
.maincontact h2{text-align: center; font-family: zhengcu; font-size:40px; color:#080808; 
	line-height: 40px; margin-bottom: 60px; padding-top: 80px; }	
.maincontact h3{font-size:18px; font-family: pingfang; color:#080808;
