.maincontact .conTxt2 p{margin-bottom: 20px;}
.maincontact .button1{height:69px; width:299px; position: relative; border: 1px solid #808080; background: #080808; 
 margin:0 auto;}
.maincontact .button1 a{display:block; width:100%; height:100%;
 text-align: center; line-height: 69px; font-size: 16px; position: relative;
  z-index: 999;
  font-family: pingfang; color:#fff;}
.maincontact .button1::before,
.maincontact .button1::after{ 
    top: 0;
    width: 50%;
    height: 100%;
    background-color: #000;
    position: absolute;
    z-index: -1;
    display: block;
    content: '';
    box-sizing: border-box;
    -webkit-transition: all linear .2s;
    transition: all linear .2s;
}
.maincontact .button1::before{ left: 0; }
.maincontact .button1::after{ right: 0; }
.maincontact .button1:hover::before,
.maincontact .button1:hover::after{
	z-index: 99;/*  background: #fff; */ width: 0;
}
.maincontact .button1:hover{ background: #fff; color: #333 }
.maincontact .button1:hover a{ color: #333 }

.maincontact .subTitle{font-size: 16px; font-family: pingfangL;
 width:640px; margin:0 auto; text-align: center; margin-bottom: 50px}
.maincontact .content .conTxt,
.maincontact .content .conTxt2,
.maincontact .content .button1{ opacity: 0; animation-delay: 1200ms }
</style>


