<template>
<ul class="ppjjList">
  <li v-for="item in items" :class="{active:item.isActive}">
    <router-link :to="item.path">{{item.text}}</router-link>
  </li>
</ul>
</template>

<script>
export default {
  name: 'ppjjList',
  data () {
    return {
      items: [
        { text: '品牌简介', isActive: false, path: 'ppjj' },
        { text: '关于我们', isActive: false, path: 'gywm' },
        { text: '公司地址', isActive: false, path: 'gsdz' }
      ]
    }
  },
  props: [ 'isActive']
}
</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.ppjjList li a{ height: 55px; line-height: 55px; border-bottom: 1px dashed #ccc;
background: url('../assets/lineIcon.jpg') 10px center no-repeat}
.ppjjList li a{ display: inline-block; color: #333; text-decoration: none;
 padding-left: 30px; width: 255px; font-weight: bold; font-size: 15px }
.ppjjList li img{ margin-left: 10px; vertical-align: middle }
.ppjjList li a.router-link-active{ background: #f1f1f1 url('../assets/lineIcon.jpg') 10px center no-repeat }
</style>




