<template>
  <div class="layout-header fadeIn">
  
    <div class="box pc">
      <div class="lbox"><router-link to="/index"><img width="92px" src="../assets/logo.png" alt=""></router-link></div>
      <div class="rbox">
        <ul>
          <li v-for="item in items">
            <span class="borderT"></span>
            <span class="borderB"></span>
            <router-link :to="item.path">{{item.text}}</router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'layout-header',
  components: {  },
  data () {
    return {
      items: [
        { text: '项目案例', path: 'xmal' },
        { text: '企业动态', path: 'qydt' },
        { text: '关于我们', path: 'ppjj' },
        { text: '联系我们', path: 'lxwm' }
      ]
    }
  }

}



$(function(){
   // 判断手机端
  if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
      console.log("mobile")
  } else {
      console.log("pc");
  }

  $(document).scroll(function() {
    var top=$(document).scrollTop();
    console.log("top="+top)
    if(top>0){
      $(".layout-header .box.pc").css({"height":"78px","background":"rgba(255,255,255,0.85)"})
      $(".layout-header img").attr("width","80px")
    }
    if(top>90){
      $(".layout-header .box.pc").css({"height":"40px","background":"rgba(255,255,255,0.85)"})
      $(".layout-header img").attr("width","60px")
    }

  })
})


</script>

<style scoped>

/* 开始过渡阶段,动画出去阶段 */
.fade-enter-active,
.fade-leave-active{
  transition: all 0.5s ease-out;
}
/* 进入开始 */
.fade-enter{
  transform: translateY(-500px);
  opacity: 0;
}
/* 出去终点 */
.fade-leave-active{
  transform: translateY(500px);
  opacity: 1;
}

.layout-header{ position: relative; z-index: 1}
.layout-header .box{  background: #fff; color: #b2b2b2; height: 78px; max-width: 100%; position: fixed; top:0; left: 0; right: 0; transition: all .3s;  padding:0 15%; z-index: 2}
.w1200{ width: 1200px; margin: 0 auto; }
.layout-header .box{ display: flex; justify-content: space-between; align-items: center;   }
.layout-header .box img{ transition: all .3s; width: 92px; }
ul{ display: flex; justify-content: space-between }
.rbox li{ font-size: 14px; margin-left: 65px; position: relative; }
.rbox li a{ font-family:pingfangB; color: #333; text-decoration: none; font-weight: bold; display: inline-block; font-size: 16px }
.rbox li .borderT{ background: #333; position: absolute;
top: -15px; right: 0;  width: 100%; height: 1px; opacity: 0; transition: all ease .3s}
.rbox li .borderB{ background: #333; position: absolute;
bottom: -15px; right: 0;  width: 100%; height: 1px; opacity: 0; transition: all ease .3s }
.rbox li:hover .borderT{ opacity: 1; top:-5px;}
.rbox li:hover .borderB{ opacity: 1; bottom: -5px;}
.layout-footer{ background: #e3e4e8; height: 80px; line-height: 80px; text-align: center; color: #333;
width: 100%; font-weight: bold }

.mobile .fade{ background: #000; opacity: .2; width: 100%; height: 100%;
position: fixed; top: 0; }
 .fadeIn {
    animation: fadeIn 0.4s ease-in 0s forwards;
    -webkit-animation: fadeIn 0.4s ease-in 0s forwards;
}
</style>






