import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import gywm from '@/pages/ppjj/gywm'
import gsdz from '@/pages/ppjj/gsdz'
import ppjj from '@/pages/ppjj/ppjj'
import lxwm from '@/pages/lxwm'
import xmal from '@/pages/xmal'
import qydt from '@/pages/qydt'
import index_content from '@/pages/index_content'

// 移动端页面
import m_index from '@/pages/m_index'
import m_xmal from '@/pages/m_xmal'
import m_qydt from '@/pages/m_qydt'
import m_gywm from '@/pages/m_gywm'
import m_lxwm from '@/pages/m_lxwm'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  mode: 'history',   //开启history模式，即可以前进、返回
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      children: [
        { path: '', name: 'index_content', component: index_content },
        { path: '/ppjj', name: 'ppjj', component: ppjj },
        { path: '/gywm', name: 'gywm', component: gywm },
        { path: '/gsdz', name: 'gsdz', component: gsdz }
      ]
    },
    {
      path: '/lxwm',
      name: 'lxwm',
      component: lxwm,
    },
    {
      path: '/xmal',
      name: 'xmal',
      component: xmal,
    },
    {
      path: '/qydt',
      name: 'qydt',
      component: qydt,
    },
    {
      path: '/m_index',
      name: 'm_index',
      component: m_index
    },
    {
      path: '/m_xmal',
      name: 'm_xmal',
      component: m_xmal
    },
    {
      path: '/m_qydt',
      name: 'm_qydt',
      component: m_qydt
    },
    {
      path: '/m_gywm',
      name: 'm_gywm',
      component: m_gywm
    },
    {
      path: '/m_lxwm',
      name: 'm_lxwm',
      component: m_lxwm
    }
  ]
})



