<template>
<div class="index">
  <layout-header />
  <div class="lxwm w910">
  	<img class="lxwmImg" src="@/assets/lxwmImg.jpg" alt="">
  	<p class="text">{{msg}}</p>
  	<div class="box">
  		<div class="lbox"><span class="yellow">必填</span>公司名</div>
  		<div class="rbox"><input type="text" v-model="cmpName" class="inputC"></div>
  	</div>
  	<div class="box">
  		<div class="lbox"><span class="yellow">必填</span>访客名</div>
  		<div class="rbox"><input type="text" v-model="vistorName" class="inputC"></div>
  	</div>
  	<div class="box">
  		<div class="lbox"><span class="yellow">必填</span>电话号码</div>
  		<div class="rbox"><input type="text" v-model="telName" class="inputC"></div>
  	</div>
  	<div class="box">
  		<div class="lbox"><span class="yellow">必填</span>留言</div>
  		<div class="rbox"><textarea v-model="msgName" class="textareaC"></textarea></div>
  	</div>
  	<div class="sendMsg">发信息</div>
  </div>
  <layout-footer />
</div>
</template>

<script>
import layoutHeader from '@/components/layoutHeader'
import layoutFooter from '@/components/layoutFooter'
export default {
	name: 'index',
	components: { layoutHeader, layoutFooter},
	data () {
		return {
			msg: 'INNOCUBER是一支集策划、设计、研发为一体的创新型团队，新颖、实用、品质是我们的设计理念；我们拥有丰富的产品开发经验，并且承接过国内外多个项目；团队成员来自国内外最具创意和想法的设计师与开发团队。INNOCUBER服务于复合型新消费场景打造、产品开发、互联网营销方案、品牌打造与曝光等。商务合作，请在下框输入您的信息。',
			cmpName: '',
			vistorName: '',
			telName: '',
			msgName: ''
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


img.lxwmImg{ width: 100% }
.w910{ width: 910px; margin: 0 auto; }
.lxwm{ margin-top: 130px }
.text{ font-family: pingfangL;    font-size: 14px;
    line-height: 30px;    border-bottom: 1px solid #999999;
    padding-bottom: 30px;    color: #080808; }
.box{display: flex;    min-height: 90px;    justify-content: space-between;
    padding: 0 15px;    font-size: 14px;    border-bottom: 1px solid #999999;
    align-items: center; }
.yellow{display: inline-block;    background: #ffff00;    margin-right: 8px;}
.inputC{width: 563px;    height: 42px;    line-height: 42px;    padding-left: 5px;
    background: #f6f6f6;    border: 1px solid #ccc;}
.textareaC{width: 563px;    height: 260px;    margin: 20px 0;    padding-left: 5px;
    background: #f6f6f6;    border: 1px solid #ccc;}
.sendMsg{display: block;    width: 540px;    height: 68px;    line-height: 68px;
    text-align: center;    background: #333333;    color: #fff;
    margin: 30px auto; cursor: pointer}
</style>


