<template>
  <div class="layout-header-m">
  	<transition name="fade">
  		<div class="fade" v-if="isActive" transition="fade" @click="toggle()"></div>
  	</transition>
  	<div class="sideBar" :class="{toggle:sideBarActive}">
  		<div class="bg"></div>
  		<ul>
  			<li v-for="item in items" class="animated"
  				:class="isActive?'fadeInRight':'fadeOutRight resetDelay'"  
  			>
  				<router-link :to="item.path">{{item.text}}</router-link>
  			</li>
  		</ul>
  		<div class="ljlx">
  			<router-link to="/m_lxwm">
  				<i class="iconfont icon-xinfeng"></i>立即联系
  			</router-link>
  		</div>
  	</div>
    <div class="head" :class="{bgNone:bgActive}">
      <router-link to="m_index" id="logo"><img class="logo_img" :src="logoSrc" :class="{logo_opacity:logoToggle}" alt=""></router-link>
      <div class="switch" @click="toggle()" :class="{active:isActive}">
    		<div class="line1"></div>
    		<div class="line2"></div>
    	</div>
    </div>
  </div>
</template>

 <!-- 
34      如果要使用动画效果:
35         第一种方式是使用transition标签加css样式结合完成：
36         1.给需要运动的元素加入transition标签
37         2.默认情况下如果控制了transition标签元素的显示和隐藏它会默认
38         给这个元素加入一些class
39             隐藏: 加入类名:
40                     v-leave  
41                     v-leave-active 
42                     v-leave-to
43 
44             显示: 加入类名:
45                     v-enter  准备进行运动的状态(起始状态)
46                     v-enter-active  整个运动状态
47                     v-enter-to  整个运动状态(强调运动的结果，结束状态)
48         3.将来如果给transition设置一个name为“show”以后,将来所有的类的名称都需要改变，默认前缀为v-
49           如果加入了name属性，需要将v- 改为show-
50  -->

<script>
export default {
  name: 'layout-header-m',
  components: {  },
  data () {
  	return {
  		isActive: false,
  		sideBarActive: false,
      logoToggle: false,  //默认显示logo
      bgActive: false,  //默认背景不显示

      //在项目中遇到需要动态的改变图片路径，图片路径并非是从后台获取过来的数据。
      // 因此在data中必须用require加载，否则会当成字符串来处理。
      logoSrc: require('../assets/logo_m.png'),
  		items:[
  			{ text:'项目案例', path: 'm_xmal'},
  			{ text:'企业动态', path: 'm_qydt'},
  			{ text:'关于我们', path: 'm_gywm'},
  			{ text:'联系我们', path: 'm_lxwm'},
  		]
  	}
  },
  methods: {
  	toggle(sideBarActive) { 
  		// 把data里的变量继承过来重新封装一下，用that（其他命名也行）来替代this
  		let that = this
  		// this.sideBarActive = false
  		this.isActive = !this.isActive 
  		if(!this.isActive){  //侧边栏隐藏
        this.logoToggle = true;
        setTimeout(function(){
          that.logoToggle = false   //小结：vue中的setTimeout()得用that替换
          that.bgActive = false
        },700)
        this.logoSrc = require('../assets/logo_m.png')
  			var t=setTimeout(function(){
  				that.sideBarActive = false
  				// console.log("true")
  			},500)
  			// $("ul li").addClass("resetDelay")
  		} else {   //侧边栏显示
  			this.sideBarActive = true
        this.logoSrc = require('../assets/logo_m_w.png')
        this.bgActive = true
  			// $("ul li").removeClass("resetDelay")
  			// console.log("false")
  		}
  	},
  }
}



$(function(){
	
})


</script>

<style scoped>
.head{ text-align: center; position: fixed; padding:2vh 0; z-index: 4;
	top: 0; left: 0; right: 0; background: rgba(255, 255, 255, .6); 
  box-shadow: 0 2px 3px rgba(204,204,204,0.35)
}
.bgNone{ background: none; box-shadow: none }
.fade{ background: rgba(0, 0, 0, .3); position: fixed; top: 0; left: 0; right: 0; width: 100%;
height: 100%; z-index: 2; opacity: 1; transition: all .3s }
.sideBar{    width: 75%;    height: 100%;    position: fixed;    top: 0;    right: -75%;    transition: all .3s;    z-index: 3;    opacity: 1;}
.sideBar .bg{     background: rgba(42,41,52,0.9);    width: 100%;
    height: 100%;    position: absolute;    top: 0;    left: 0 }
.toggle{ right: 0; }

#logo{  }
 .logo_img{ width: 19.86%; margin: 0 auto; transition: all .3s}
 .logo_opacity{ opacity: 0 }
.switch{ position: absolute; top: 10px; right: 10px;
    z-index: 4; width: 40px; height: 40px; }
.line1, .line2{width: 20px;
    height: 3px;    background: #333333;    position: absolute;    top: 0;    bottom: 0;    left: 0;    right: 0;    opacity: 1;    display: inline-block;    transition: transform .3s ease-out 0s,-webkit-transform .3s ease-out 0s;}
.line1 {    top: 11px;    left: 11px;}
.line2 {    top: 20px;    left: 11px;}
.active .line1,.active .line2{ background: #fff }
.active .line1 { transform:translateY(5px) rotate(135deg) }
.active .line2 { transform:translateY(-4px) rotate(45deg) }

/*.fade-enter{} 元素初始状态 
.fade-enter-active{}  元素最终状态  
.fade-leave-active{} 元素离开的最终状态
*/
.fade-enter-active, 
.fade-leave-active {
  transition: opacity .3s
}
.fade-enter, 
.fade-leave-active {
  opacity: 0
}

ul{z-index: 2;   margin-top: 140px;position: relative; text-align: left }
ul li{ margin: 0 7% 0 10%;    border-bottom: 1px solid rgba(255,255,255,0.5);
    animation-duration: .2s; animation-delay: 1s; }
ul li:first-child{ animation-delay: .2s }
ul li:nth-child(2){ animation-delay: .3s }
ul li:nth-child(3){ animation-delay: .4s }
ul li:last-child{ animation-delay: .5s }
ul li.resetDelay{ animation-delay: 0s }

ul li a{color: #fff;    font-size: 16px;    font-family: pingfangB;    padding: 15px 0 15px 0px;    display: block;}
.ljlx{ position: relative; z-index: 2; border: 1px solid rgba(255,255,255,.5);
text-align: center; /*background: url('~@/assets/lxIcon.png') 29% center no-repeat;
background-size: 8%;*/
height: 6.5vh; line-height: 6.5vh; margin: 4vh 7% 0 10%; }
.icon-xinfeng{ font-size: 22px; vertical-align: middle; margin-right: 5px; }
.ljlx a{ font-size: 4vw; font-family: pingfangB; color: #fff; text-decoration: none; display: inline-block; width: 100%; }
</style>






