			<h2 style="display: none" class="fadeIn">服务内容</h2>
			<p class="subTitle" :class="{fadeIn:fwnrFadeIn}">{{subTitleMsg}}</p>
			<ul :class="{fadeIn:fwnrFadeIn}">
				<li v-for="item in items">
					<div class="serPic"><img :src="item.imgSrc"></div>
					<p>{{item.msg}}</p>
				</li>
			</ul>
			
		</div>
	</section>
</template>

<script>
// import layoutHeader from '@/components/layoutHeader'
// import layoutFooter from '@/components/layoutFooter'

export default {
	name: 'service',
	components: {  },
	data () {
		return {
			subTitleMsg: 'INNOCUBER的服务内容包含互联网产品从设计到研发的整套方案，在空间设计方面，拥有民宿空间与商用空间两个复合型消费场景的打造方案。',
			items:[
				{ imgSrc: require('../../assets/serPic1.png'), msg:'民宿空间设计' },
				{ imgSrc: require('../../assets/serPic2.png'), msg:'商业空间设计' },
				{ imgSrc: require('../../assets/serPic3.png'), msg:'UI/WEB设计' },
				{ imgSrc: require('../../assets/serPic4.png'), msg:'APP产品研发' },
				{ imgSrc: require('../../assets/serPic5.png'), msg:'品牌包装' }
			]
		}
	},
	props: [ 'fwnrAnimation', 'fwnrFadeIn' ]
}
</script>

<style scoped>
/*service*/
.service{padding:80px 0 60px;} 
.service h2{text-align: center; font-family: zhengcu; font-size:40px; color:#080808; 
	line-height: 40px; margin-bottom: 60px; opacity: 0}
.service .subTitle{font-size: 16px; font-family: pingfangL;
 width:640px; margin:0 auto; text-align: center; margin-bottom: 50px;
  opacity:0; line-height: 32px; animation-delay: 1200ms}
.service ul{display:flex;   justify-content: space-around; align-items: center;
	margin-bottom: 80px; opacity: 0; animation-delay: 1200ms }
.service ul li{width:150px;  text-align: center}
.service ul li .serPic{width:100%; height:48px; 
	text-align: center;  }
.service ul li img{margin:0 auto;}
.service ul li:first-child img{display:block; width:46px;}
.service ul li:nth-child(2) img{display:block; width:39px;}
.service ul li:nth-child(3) img{display:block; width:26px;}
.service ul li:nth-child(4) img{display:block; width:30px;}
.service ul li:last-child img{display:block; width:37px;}
.service ul li p{font-size: 18px; font-family: pingfangB; }
.service .button1{height:69px; width:299px; border: 1px solid #080808; background: #080808; 
 margin:0 auto; position: relative; opacity: 0; animation-delay: 1200ms}
.service .button1 a{display:block; width:100%; height:100%;
 text-align: center; line-height: 69px; font-size: 16px; position: relative;
  z-index: 999;
  font-family: pingfangB; color:#fff;}
.service .button1::before,
.service .button1::after{ 
    top: 0;
    width: 50%;
    height: 100%;
    background-color: #000;
    position: absolute;
    z-index: -1;
    display: block;
    content: '';
    box-sizing: border-box;
    -webkit-transition: all linear .2s;
    transition: all linear .2s;
}
.service .button1::before{ left: 0; }
.service .button1::after{ right: 0; }
.service .button1:hover::before,
.service .button1:hover::after{
	z-index: 99;/*  background: #fff; */ width: 0;
}
.service .button1:hover{ background: #fff; color: #333 }
.service .button1:hover a{ color: #333 }
</style>


